import {createGlobalStyle} from "styled-components";
import styledNormalize from "styled-normalize";

const GlobalStyle = createGlobalStyle`
  ${styledNormalize}
  
  *,
  *:before,
  *:after {
    box-sizing: inherit;
  }

  html {
    font-size: 62.5%;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    box-sizing: border-box;
  }

  body {
    background: #f8fafc;
    font-family: 'Open Sans', sans-serif; 
    line-height: 1.5;
    padding: 50px 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  img {
    max-width: 100%;
  }
  
  .categories-bar{
    position: fixed;
    top: 200px;
    left: calc(((((100vw - 870px) / 2) - 150px) / 3) + 15px);
    max-width: min(calc((100vw - 870px) / 2 - 50px), 200px);
    max-height: 40vh;
    overflow-x: hidden;
    text-overflow: ellipsis;
    
    &.show-progress > div{
      position: relative;
    }
    
    &.show-progress > div::before{
      content: "";
      position: absolute;
      border-left: 3px solid black;
      height: 94.5%;
      left: 2px;
      top: 50%;
      opacity: 0.3;
      transform: translateY(-50%);
    }
    
    & > a:first-of-type{
      display: inline-block;
    }
    
    .same-line{
      display: inline-block;
      * {
        display: inline;
      }
      margin-bottom: 3px;
    }
    
    .title{
      font-size: 25px;
      color: #000;
      font-weight: 700;
      letter-spacing: -0.01em;
    }
    
    .sub-title{
      font-size: 14px;
      font-weight: 700;
      color: #333333;
      word-spacing: 1px;
      letter-spacing: -0.06em;
    }
    
    .category-wrap{
      position: relative;
      padding: 5px 12px;
    
      a{
        text-decoration: none;
        .sub-links{
          font-size: 16px;
          font-weight: 700;
          opacity: 0.3;
          transition: opacity 100ms ease;
          color: #000;
          white-space: nowrap;
          overflow-x: hidden;
          text-overflow: ellipsis;
        }
      }
      &::before{
        content: "";
        position: absolute;
        border-left: 3px solid black;
        transition: opacity 100ms ease;
        height: 75%;
        left: 2px;
        top: 50%;
        transform: translateY(-50%);
        opacity: 0;
      }
      
      &:hover{
        cursor: pointer;
      }
    }
    .category-wrap[data-is-current], .category-wrap:hover{
      .sub-links{
        opacity: 1;
        transition: opacity 100ms ease;
      }
      
      &::before{
        opacity: 1;
        transition: opacity 100ms ease;
      }
    }
  }

  .gatsby-highlight {
    border-bottom: 1px solid #e0e6ed;
    border-top: 1px solid #e0e6ed;
    margin: 15px -90px;
    padding: 0;

    pre[class*="language-"] {
      margin: 0;
      padding: 25px 140px 27px 140px;
    }
  }
  

  pre[class*="language-"] {
    background: rgba(245, 245, 245, 1);
    color: rgb(65, 76, 94);  
  }

  @media only screen and (max-width: 870px) {
    body {
      padding: 15px 0;
    }
  
    .categories-bar{
      position: fixed;
      opacity: 0;
      background: #f9fafc;
      border: 1px solid black;
    }
  
    .gatsby-highlight {
      margin: 15px -15px;

      pre[class*="language-"] {
        padding: 25px;
      }
    }
  }
`;
export default GlobalStyle;
